body {
  font-family: Roboto, sans-serif;
}

.modal {
  z-index: 1;
  background-color: #0006;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  background-color: #fefefe;
  max-width: 90%;
  max-height: 90vh;
  margin: 0 auto;
  padding: 48px 20px 40px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%);
}

.modal-poster {
  border-radius: 5px;
  width: 264px;
}

.movie-title {
  color: #000;
  margin: 0;
  font-size: 20px;
}

.modal-desc {
  margin-top: 20px;
}

.movie-details {
  justify-content: left;
  margin-top: 20px;
  padding: 0%;
  display: flex;
}

.movie-details-variables {
  color: #8c8c8c;
  margin: 0;
  padding: 0;
  font-size: 12px;
  list-style-type: none;
}

.movie-details-values {
  color: #000;
  margin: 0 0 0 80px;
  padding: 0;
  font-size: 12px;
  list-style-type: none;
}

.movie-summary {
  color: #000;
  margin-top: 20px;
  font-size: 12px;
  font-weight: bold;
  line-height: 20px;
}

.movie-summary p {
  font-weight: 500 bold;
  color: #000;
  margin: 0;
}

.movie-summary p:first-child {
  color: #000;
  margin-bottom: 8px;
  line-height: 16px;
}

.close {
  color: #aaa;
  float: right;
  margin: 0;
  font-size: 28px;
  font-weight: bold;
  line-height: 15px;
  position: absolute;
  top: 8px;
  right: 8px;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.modal-buttons {
  display: flex;
}

.modal-button {
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #000;
  border-radius: 5px;
  width: 110px;
  margin-top: 20px;
  padding: 6px 27px;
  font-size: 12px;
}

.modal-button[disabled] {
  color: #fff;
  cursor: not-allowed;
  opacity: 1;
  background-color: #ff6b01;
}

.modal-button:first-of-type {
  margin-right: 20px;
}

.modal-button:hover {
  color: #fff;
  background-color: #ff6b01;
  border: 1px solid #ff6b01;
}

.property-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.property-list li {
  margin-bottom: 8px;
  display: flex;
}

.property-list li:last-child {
  margin-bottom: 0;
}

.property {
  color: #8c8c8c;
  align-items: center;
  width: 108px;
  margin-right: 5px;
  font-size: 12px;
  font-weight: bold;
  line-height: 16px;
}

.value {
  color: #000;
  font-size: 12px;
  font-weight: bold;
  line-height: 16px;
  display: flex;
}

.first-value {
  color: #fff;
  background-color: #ff6b01;
  border-radius: 5px;
  justify-content: space-around;
  width: 36px;
  height: 16px;
  display: flex;
}

.value-slash {
  color: #8c8c8c;
  font-size: 12px;
  font-weight: normal;
}

